@import url(https://fonts.googleapis.com/css?family=Electrolize&display=swap);
body{margin:0;font-family:'Electrolize', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#d5d5d5;background-color:black}

.background{position:fixed;top:0;right:0;bottom:0;left:0;z-index:-1}.background canvas{height:100%;width:100%}

.body{padding:2rem}.title{margin:0}.intro{overflow:auto;width:600px;max-width:100%;margin:1.5rem 0;font-size:20px}.links{margin:0;padding:0;display:inline-grid;justify-content:start;grid-auto-flow:column;grid-gap:2rem;gap:2rem}.links-item{list-style:none}.links-item a{display:inline-block;padding:5px;cursor:pointer}.links-item:hover .icon{-webkit-animation:jump 0.3s;animation:jump 0.3s}.works{margin:0;padding:0;margin:3rem 0;display:grid;justify-content:start;grid-gap:1.5rem;gap:1.5rem}.works-item{list-style:none}.works-link{position:relative;color:#d5d5d5;font-size:18px;text-decoration:none;font-weight:bold}.works-link::before{content:'';position:absolute;left:0;bottom:-3px;width:100%;height:2px;background-image:-webkit-gradient(linear, right top, left top, from(#d856bf), to(#0e5ea5));background-image:linear-gradient(to left, #d856bf, #0e5ea5);-webkit-transform:translateZ(0);transform:translateZ(0);-webkit-transition:0.2s;transition:0.2s;z-index:-1}.works-link:hover::before{height:13px}@-webkit-keyframes jump{0%,100%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(-15%);transform:translateY(-15%)}}@keyframes jump{0%,100%{-webkit-transform:translateY(0);transform:translateY(0)}50%{-webkit-transform:translateY(-15%);transform:translateY(-15%)}}

.icon{display:inline-block;font-size:0;line-height:100%;vertical-align:bottom;color:#d5d5d5}

